
body{
  margin: 0;
}
.float-l {
  float: left;
}
.float-r {
  float: right;
}
.margin-r {
  margin-right: 2em;
}
.margin-l {
  margin-left: 2em;
}
.clear {
  clear: both;
}
.border-box{
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.logo-box{
  text-align: center;
}
.logo{
  max-width: 64px;
  margin: 64px auto;
  display: block;
}
.home-links{
  text-align: center;
}

.rmedia-header{
  background: -webkit-gradient(linear, left top, left bottom, from(#dce1f1), to(transparent));
  background: linear-gradient(#dce1f1, transparent);
  height: 100px;
  text-align: center;
  border-bottom: #d4d4d4 solid 1px;
  overflow: hidden;
}
.rmedia-h1{
  color: #48525b;
  padding: 0 1em;
  font-size: 48px;
}
.rmedia-layout {
  font-size: 0;
}
.rmedia-nav,
.rmedia-main{
  display: inline-block;
  vertical-align: top;
  line-height: 1.6;
  font-size: 16px;
  color: #6a737d;
  height: calc(100vh - 100px);
  overflow: auto;
}
.rmedia-nav{
  width: 360px;
  border-right: #d4d4d4 solid 1px;
  padding: 1em;
  font-size: 14px;
}
.rmedia-main{
  width: calc(100% - 360px);
  padding: 1em;
  position: relative;
}
.rmedia-h2,
.rmedia-h3{
  color: #4168a4;
  font-weight: normal;
}
.tree{
  list-style: none;
  padding: 0;
  margin: 0;
}
.tree-item .tree{
  padding-left: 1em;
}
.tree-name{
  cursor: pointer;
  display: inline-block;
  color: #555;
  border: 1px solid transparent;
  padding: .2em .5em;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.tree-name:hover{
  border-color: #4168a4;
}
.tree-name.active{
  background: #c2d5f2;
}
.rmedia b{
  color: #b970af;
}
.rmedia-section,
.rmedia-art{
  overflow: hidden;
}
